import React, {useContext, useEffect, useRef, useState} from 'react';
import {Button, Form, Input, Space, Table} from "antd";
import { SearchOutlined } from '@ant-design/icons';
import Highlighter from 'react-highlight-words';
import axios from "axios";
import AuthContext from "../context/AuthProvider";
import "./login.scss";

const Login = () => {
    const [form] = Form.useForm();
    const { auth, setAuth } = useContext(AuthContext);
    const [data, setData] = useState();
    const [searchText, setSearchText] = useState('');
    const [searchedColumn, setSearchedColumn] = useState('');
    const searchInput = useRef(null);
    const headers = {
        'Accept': 'application/json',
        'Content-Type': 'application/x-www-form-urlencoded'
    };

    const onSubmit = () => {
        form?.validateFields().then((data) => {
            const newData = new FormData();
            newData.append("_username", data._username)
            newData.append("_password", data._password)
            newData.append("_subdomain", data._subdomain)
            axios.post(`https://${form.getFieldValue("_subdomain")}.ox-sys.com/security/auth_check`, newData,{headers}).then((res) => {
                if (res.status === 200) {
                    setAuth(res?.data?.token)
                }
            })
        })
    };

    useEffect(() => {
        if (auth.length) {
            axios.get("https://toko.ox-sys.com/variations", {
                headers: {
                    'Authorization': `Bearer ${auth}`,
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                }
            }).then((res) => {
                setData(res.data.items)
            })
        }
    }, [auth])

    const handleSearch = (selectedKeys, confirm, dataIndex) => {
        confirm();
        setSearchText(selectedKeys[0]);
        setSearchedColumn(dataIndex);
    };

    const handleReset = (clearFilters) => {
        clearFilters();
        setSearchText('');
    };

    const getColumnSearchProps = (dataIndex) => ({
        filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
            <div
                style={{
                    padding: 8,
                }}
            >
                <Input
                    ref={searchInput}
                    placeholder={`Search ${dataIndex}`}
                    value={selectedKeys[0]}
                    onChange={(e) => setSelectedKeys(e.target.value ? [e.target.value] : [])}
                    onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
                    style={{
                        marginBottom: 8,
                        display: 'block',
                    }}
                />
                <Space>
                    <Button
                        type="primary"
                        onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
                        icon={<SearchOutlined />}
                        size="small"
                        style={{
                            width: 90,
                        }}
                    >
                        Search
                    </Button>
                    <Button
                        onClick={() => clearFilters && handleReset(clearFilters)}
                        size="small"
                        style={{
                            width: 90,
                        }}
                    >
                        Reset
                    </Button>
                    <Button
                        type="link"
                        size="small"
                        onClick={() => {
                            confirm({
                                closeDropdown: false,
                            });
                            setSearchText(selectedKeys[0]);
                            setSearchedColumn(dataIndex);
                        }}
                    >
                        Filter
                    </Button>
                </Space>
            </div>
        ),
        filterIcon: (filtered) => (
            <SearchOutlined
                style={{
                    color: filtered ? '#1890ff' : undefined,
                }}
            />
        ),
        onFilter: (value, record) =>
            record[dataIndex].toString().toLowerCase().includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: (visible) => {
            if (visible) {
                setTimeout(() => searchInput.current?.select(), 100);
            }
        },
        render: (text) =>
            searchedColumn === dataIndex ? (
                <Highlighter
                    highlightStyle={{
                        backgroundColor: '#ffc069',
                        padding: 0,
                    }}
                    searchWords={[searchText]}
                    autoEscape
                    textToHighlight={text ? text.toString() : ''}
                />
            ) : (
                text
            ),
    });

    const columns = [
        {
            title: ' ',
            dataIndex: 'productName',
            key: 'productName',
            width: '30%',
            ...getColumnSearchProps('productName'),
            sorter: (a, b) => a.productName.length - b.productName.length,
            sortDirections: ['descend', 'ascend'],
        },
        {
            title: '',
            dataIndex: 'supplier',
            key: 'supplier',
            width: '20%',
        },
        {
            title: ' ',
            dataIndex: 'shortDescription',
            key: 'shortDescription',
        },
    ];

    return (
        <div className="login-page-wrapper">
            {auth.length ? (
                    <div className="table-wrapper">
                        <Table scroll={{ y: 850 }} columns={columns} dataSource={data} />
                    </div>

            ) : (
                <div className="login-block">
                    <div className="login-block-header">
                        <img className="company-logo" src="ox-system-logo.svg" alt=""/>
                        <h1 className="company-title">Ox system login page</h1>
                    </div>
                    <div className="login-block-body">
                        <Form form={form} onFinish={onSubmit}>
                            <Form.Item name="_username" rules={[{ required: true, message: 'Please input your username!' }]}>
                                <Input placeholder="Username" allowClear/>
                            </Form.Item>
                            <Form.Item name="_password" rules={[{ required: true, message: 'Please input your password!' }]}>
                                <Input.Password placeholder="Password" allowClear/>
                            </Form.Item>
                            <Form.Item name="_subdomain" rules={[{ required: true, message: 'Please input your company!' }]}>
                                <Input placeholder="Company" allowClear/>
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="submit">
                                    Login
                                </Button>
                            </Form.Item>
                        </Form>
                    </div>
                </div>
            )}
        </div>
    );
};

export default Login;
